/** PAGE STRUCTURE **/
@media (min-width: 700px) {
  .ne-content-wrapper {
    display: grid;
    grid-template-columns: 15% 70% 15%;
    grid-template-rows: 90% 8% 2%;
    justify-items: center;
  }
}

@media (max-width: 700px) {
  .ne-content-wrapper {
    display: grid;
    grid-template-columns: 5% 90% 5%;
    grid-template-rows: 90% 8% 2%;
    justify-items: center;
  }
}

.ne-content {
  min-height: 92vh;
  width: 100%;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

/** HEADER **/
@media (min-width: 700px) {
  .ne-header {
    padding-top: 2%;
    display: grid;
    grid-template-columns: 50% 10% 40%;
    justify-items: center;
    align-items: center;
  }
}

@media (max-width: 700px) {
  .ne-header {
    display: grid;
    grid-template-rows: 25% 25% 1fr;
    justify-items: center;
    align-items: center;
    margin-bottom: 30px;
  }
}

.ne-header-list {
  list-style-type: none;
  display: flex;
  align-items: center;
  flex-flow: column;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.ne-header-list-item-selected {
  text-decoration: underline;
}

.ne-header-title:hover, .ne-logo:hover, .ne-header-list-item:hover {
  color: #555555;
  cursor: pointer;
}

.ne-logo {
  height: 50px;
  width: 50px;
}

/** RELEASE DETAIL **/
@media (min-width: 700px) {
  .ne-release-detail {
    padding-top: 25px;
    display: grid;
    grid-template-columns: 50% 50%;
  }

  .ne-release-left {
    padding-right: 25px;
  }

  .ne-release-details {
    padding-left: 25px;
  }
}

@media (max-width: 700px) {
  .ne-release-detail {
    display: grid;
    grid-template-rows: 215px 1fr;
  }
}

.ne-release-detail-header {
  display: flex;
  flex-direction: column;
}

.ne-release-detail-play-button {
  font-size: 10px;
  cursor: pointer;
}

.ne-release-image {
  padding-bottom: 25px;
}

.ne-release-detail-zine {
  margin-top: 25px;
  margin-bottom: 25px;
  display: flex;
  justify-content: center;
}

.ne-release-detail-zine .ne-zine-brief {
  width: 50%;
}

/** RELEASES **/
@media (min-width: 700px) {
  .ne-releases {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .ne-release-section {
    grid-template-columns: 1fr 1fr 1fr;
    grid-column: 1 / span 3;
    display: grid;
    margin-bottom: 10px;
  }

  .ne-release-section-title {
    grid-column: 1 / span 3;
    text-align: center;
  }
}

@media (max-width: 700px) {
  .ne-releases {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .ne-release-section {
    grid-template-columns: 1fr 1fr;
    grid-column: 1 / span 2;
    display: grid;
    margin-bottom: 10px;
  }

  .ne-release-section-title {
    grid-column: 1 / span 2;
    text-align: center;
  }
}

.ne-release-brief {
  display: grid;
  grid-template-rows: 80% 10% 10%;
  justify-self: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px;
}

.ne-release-brief:hover {
  background-color: #bbbbbb;
  cursor: pointer;
}

.ne-release-name, .ne-release-artist {
  min-width: 125px;
  text-align: center;
}

/** ARTIST DETAIL **/
.ne-artist-detail {
  display: grid;
  grid-template-rows: 225px 1fr;
  justify-items: center;
  width: 95%;
}

.ne-artist-image {
  padding-bottom: 25px;
}

.ne-artist-details {
  text-align: center;
}

@media (max-width: 700px) {
  .ne-artist-detail-featured-releases {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
  }

  .ne-artist-detail-featured-releases .ne-release-name {
    min-width: 0px;
  }
}

@media (min-width: 700px) {
  .ne-artist-detail-featured-releases {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

/** ARTISTS **/
@media (min-width: 700px) {
  .ne-artists {
    grid-template-columns: 1fr 1fr 1fr;
    grid-column: 1 / span 3;
    display: grid;
    margin-bottom: 10px;
    height: 140px;
  }
}

@media (max-width: 700px) {
  .ne-artists {
    grid-template-columns: 1fr 1fr;
    grid-column: 1 / span 2;
    display: grid;
    margin-bottom: 10px;
    height: 140px;
  }
}

.ne-artist-brief {
  display: grid;
  grid-template-rows: 60% 40%;
  justify-self: center;
  width: 90%;
  padding: 5px;
}

.ne-artist-brief:hover {
  background-color: #bbbbbb;
  cursor: pointer;
}

.ne-artist-brief-name {
  min-width: 125px;
  text-align: center;
}

.ne-artist-brief-image {
  justify-content: center;
  display: flex;
}

.ne-artist-detail-description {
  white-space: pre-wrap;
}

/** RELEASE **/
.ne-release-name, .ne-release-artist {
  font-size: 60%;
  justify-content: center;
}

.ne-release-detail-description {
  white-space: pre-wrap;
}

.ne-release-detail-date {
  font-size: 10px;
  margin-bottom: 25px;
}

.ne-release-left {
  justify-content: center;
  margin-bottom: 25px;
}

.ne-release-image {
  justify-content: center;
  display: flex;
}

/** THOUGHTS **/
@media (min-width: 700px) {
  .ne-thoughts {
    margin-left: 100px;
    margin-right: 100px;
  }
}

.ne-thought {
  white-space: pre-wrap;
  margin-bottom: 25px;
}

.ne-thought-title {
  font-size: 18px;
  font-weight: bold;
}

.ne-thought-date {
  font-size: 10px;
  margin-bottom: 5px;
  font-style: italic;
}

.ne-thought-content {
  font-size: 14px;
  margin-bottom: 10px;
}

.ne-thought-image {
  max-width: 250px;
  max-height: 250px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.ne-thought-image-wrap {
  width: 100%;
}

/** ABOUT **/
@media (min-width: 700px) {
  .ne-about {
    margin-left: 100px;
    margin-right: 100px;
  }  
}

.ne-about {
  text-align: center;
}

.ne-bio-name {
  font-size: xx-large;
  font-weight: 100;
  font-family: serif;
}

.ne-bio-desc {
  white-space: pre-wrap;
}

.ne-bio-email {
  font-size: x-small;
}

.ne-bio-gram {
  margin-right: 5px;
}

.ne-about-general {
  margin-top: 15px;
  font-size: large;
}

/** FOOTER **/
.ne-footer-wrap {
  grid-column: 1 / span 3;
  width: 100%;
  align-self: end;
}

.ne-footer-spacer {
  display: block;
  height: 25px;
  width: 100%;
}

@media (min-width: 700px) {
  .ne-footer {
    width: 100%;
    display: grid;
    grid-template-columns: 10fr 1fr 1fr 1fr 10fr;
    justify-content: center;
    align-items: center;
    justify-items: center;
  }
}

@media (max-width: 700px) {
  .ne-footer {
    width: 100%;
    display: grid;
    grid-template-columns: 5fr 1fr 1fr 1fr 5fr;
    justify-content: center;
    align-items: center;
    justify-items: center;
  }
}

.ne-audio-player-wrapper {
  grid-column: 1 / span 3;
  margin-top: 10px;
  width: 100%;
}

/** ZOOMABLE IMAGE **/
.ne-zoomable-image:hover {
  cursor: pointer;
}

.ne-zoomable-image {
  max-width: 100%;
}

.ne-random-image {
  max-width: 100%;
  max-height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/** ZINES **/
@media (min-width: 700px) {
  .ne-zines {
    grid-template-columns: 1fr 1fr 1fr;
    grid-column: 1 / span 3;
    display: grid;
    margin-bottom: 10px;
    height: 140px;
  }
}

@media (max-width: 700px) {
  .ne-zines {
    grid-template-columns: 1fr 1fr;
    grid-column: 1 / span 2;
    display: grid;
    margin-bottom: 10px;
    height: 140px;
  }
}

.ne-zine-brief {
  display: grid;
  grid-template-rows: 60% 40%;
  justify-self: center;
  width: 90%;
  padding: 5px;
}

.ne-zine-brief:hover {
  background-color: #bbbbbb;
  cursor: pointer;
}

.ne-zine-brief-image {
  justify-content: center;
  display: flex;
}

.ne-zine-brief-name {
  min-width: 125px;
  text-align: center;
}

.ne-zine-detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.ne-zine-page-image-scrollable {
  margin-bottom: 15px;
  height: auto;
}

.ne-zine-button {
  justify-self: center;
  cursor: pointer;
}

.ne-fugue-image {
  max-height: 700px;
}

body {
  color: #eeeeee;
  background-color: #111111;
}

.ne-stars:hover {
  cursor: pointer;
}

.ne-fugue-next {
  cursor: pointer;
  font-weight: bold;
}
